<template>
  <div class="launder-bussiness">
    <div class="topTitle">
      <span class="topTitle-span1">洗钱风险评估</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">业务洗钱风险评估</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">业务管理</span>
      <div class="button-content">
        <Button type="primary" @click="operate('add')" v-authcontrol="{menuCode:'106001001001'}" v-if="firstLoginFlag">
          <i class="ivu-icon ivu-icon-md-add"></i>新增业务
        </Button>
        <Button type="primary" @click="exportFn" v-authcontrol="{menuCode:'106001001006'}" v-if="firstLoginFlag">
          <i class="ivu-icon ivu-icon-md-arrow-up"></i>导出
        </Button>
      </div>
    </div>
    <div class="header" >
      <div class="content1 border-bottom">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>业务类型
        </div>
        <div class="header-wrapper">
          <Tag
            @click.native="changeSts(index,item.checked)"
            checkable
            v-for="(item, index) in businessTypes"
            :checked="item.checked"
            :key="index"
          >{{item.name}}</Tag>
        </div>
      </div>
      <div class="content1">
        <div class="content1-1">
          <div class="contentCellTitle">
            <div class="contentCellTitle-1"></div>业务部门
          </div>
          <treeselect
            v-model="formValidate.department"
            valueConsistsOf="ALL" 
              :limit="3"
            :multiple="true"
            :options="data1"
            placeholder
            style="width: 300px;
                    display: inline-block;
                    vertical-align: -10px;
                    margin-top: 10px;"
          />
          <div class="checked-content">
            <span class="title">风险等级</span>
            <CheckboxGroup v-model="formValidate.riskLevel">
              <Checkbox
                v-for="item in riskLevels"
                :label="item.levelCode"
                :key="item.levelCode"
              >{{item.levelName}}</Checkbox>
            </CheckboxGroup>
          </div>
          <div class="checked-content">
            <span class="title">状态</span>
            <RadioGroup v-model="formValidate.flag">
              <Radio label="">未选择</Radio>
              <Radio v-for="item in flags" :label="item.code" :key="item.code">{{item.name}}</Radio>
            </RadioGroup>
          </div>
          <div class="checked-content border-top">
            <span class="title">审核状态</span>
            <CheckboxGroup v-model="formValidate.evalStatus">
              <Checkbox
                :label="item.code"
                v-for="item in evalStatuss"
                :key="item.code"
              >{{item.name}}</Checkbox>
            </CheckboxGroup>
            <CheckboxGroup v-model="formValidate.adjStatus">
              <Checkbox :label="item.code" v-for="item in adjStatuss" :key="item.code">{{item.name}}</Checkbox>
            </CheckboxGroup>
          </div>
        </div>
      </div>
    </div>
    <div class="content" style="overflow:hidden">
      <div class="content-detail" v-for="(carData,key) in cardDatas" :key="key">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>
          {{key}}
        </div>
        <div class="detail" v-for="(cardTypData, index) in carData" :key="index">
          <div class="head">
            <img src="../../../assets/money-rist-head.png" alt />
            <el-popover
                placement="top-start"
                title
                width="200"
                trigger="hover"
                :content="cardTypData.busName">
                <p
                    slot="reference"
                    class="special-title"
                    @click="operate('detail',cardTypData)"
                    >{{cardTypData.busName}}</p>
            </el-popover>
            <i-switch
              v-model="cardTypData.flag"
              @on-change="changeUseSts(cardTypData, key, index)"
              v-authcontrol="{menuCode:'106001001005'}" v-if="firstLoginFlag"
              true-value="1"
              false-value="0"
            >
              <span slot="open">启用</span>
              <span slot="close">停用</span>
            </i-switch>
            <div class="card-sts1" v-if="cardTypData.evalStatus==0||cardTypData.evalStatus==3">
              <p>待评估</p>
            </div>
          </div>
          <div class="center">
            <div class="left">
              <span class="bus-debt">业务部门</span>
              <span class="manage-debt">{{cardTypData.dpname}}</span>
              <div class="steps">
                <el-steps :active="Number(cardTypData.workflow)-1" :align-center="true">
                  <template v-for="it in cardTypData.bpmDefinitions">
                    <el-step
                      :title="it.nodeName"
                      :key="it.id"
                      status="success"
                      v-if="it.nodeId<cardTypData.workflow"
                      @click.native="showProcess(cardTypData,it.nodeId)"
                    ></el-step>
                    <el-step
                      :title="it.nodeName"
                      :key="it.id"
                      :status="getDisplayStatus(cardTypData)"
                      v-if="it.nodeId==cardTypData.workflow"
                      @click.native="showProcess(cardTypData,it.nodeId)"
                    ></el-step>
                    <el-step
                      :title="it.nodeName"
                      :key="it.id"
                      status="wait"
                      v-if="it.nodeId>cardTypData.workflow"
                    ></el-step>
                  </template>
                </el-steps>
              </div>
              <div class="date-content">
                <span
                  class="create-date"
                >创建日期：{{cardTypData.createTime?cardTypData.createTime.substr(0,10):''}}</span>
                <span
                  class="assess-date"
                >评估日期：{{cardTypData.evalDate?cardTypData.evalDate.substr(0,10):''}}</span>
              </div>
            </div>
            <div class="right">
              <div class="risk-rank">风险等级</div>
              <img src="../../../assets/low1.png" alt v-show="cardTypData.riskLevel == '1'" />
              <img src="../../../assets/media1.png" alt v-show="cardTypData.riskLevel == '2'" />
              <img src="../../../assets/high1.png" alt v-show="cardTypData.riskLevel == '3'" />
            </div>
          </div>
          <div class="bottom">
            <div class="bottom-detail">
              <div @click="operate('edit', cardTypData)" v-authcontrol="{menuCode:'106001001002'}" v-if="firstLoginFlag">
                <img src="../../../assets/edit.png" alt />
                <div class="text">编辑</div>
              </div>
              <div @click="operate('assess', cardTypData)" v-authcontrol="{menuCode:'106001001003'}" v-if="firstLoginFlag">
                <img src="../../../assets/assess.png" alt />
                <div class="text">评估</div>
              </div>
              <div @click="operate('adjust', cardTypData)" v-authcontrol="{menuCode:'106001001004'}" v-if="firstLoginFlag">
                <img src="../../../assets/adjust.png" alt />
                <div class="text">调整</div>
              </div>
              <div @click="operate('history', cardTypData)">
                <img src="../../../assets/history.png" alt />
                <div class="text">历史</div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Modal :mask-closable="false"   v-model="showModal" :title="modelTitle" class="dialog" :styles="{width: pecentWidth}">
        <component
          @switchAssessRisk="showAssessRisk"
          @cancel="cancelDialog"
          @save="saveDatas"
          :data-obj="dataObj"
          :data-objj="dataObjj"
          :is="tabview"
          width="80%"
          v-if="showModal"
        ></component>
        <div slot="footer"></div>
      </Modal>
      <Modal :mask-closable="false"  
        v-model="modal2"
        title="提示"
        :styles="{width: '40%'}"
        @on-ok="ok"
        @on-cancel="cancel"
        :closable="false"
      >
        <Form ref="formValidate2" :label-width="80">
          <FormItem label="备注">
            <Input
              v-model="formValidate2.remark"
              type="textarea"
              :autosize="{minRows: 2,maxRows: 5}"
            ></Input>
          </FormItem>
        </Form>
      </Modal>
    </div>
  </div>
</template>
<script>
import processMessage from "../common/processMessage"; //过程信息
import addBussiness from "../common/addBussiness"; //添加和修改
import assessDetail from "../common/assessDetail"; //评估详情
import assessRisk from "../common/assessRisk"; //评估
import history from "../common/history"; //历史
import adjustRiskLevel from "../common/adjustRiskLevel"; //调整
import Treeselect from "@riophae/vue-treeselect";
import "@riophae/vue-treeselect/dist/vue-treeselect.css";
export default {
  components: {
    processMessage,
    addBussiness,
    adjustRiskLevel,
    history,
    Treeselect,
    assessRisk,
    assessDetail
  },
  created() {
   if(this.$route.query.pageParams=='待评估'){
     this.formValidate.evalStatus=['0']
   }else if(this.$route.query.pageParams=='审核退回'){
     this.formValidate.evalStatus=['3'];
    this.formValidate.adjStatus=['3'];
   }else if(this.$route.query.pageParams=='审核中'){
       this.formValidate.evalStatus=['2'];
    this.formValidate.adjStatus=['2'];
   }
    let that = this;
    let data = {
      busType: this.formValidate.busType, //业务类型
      department: this.formValidate.department, //业务部门
      riskLevel: this.formValidate.riskLevel, //风险等级
      evalStatus: this.formValidate.evalStatus, //评估审核
      adjStatus: this.formValidate.adjStatus, //调整审核
      flag: this.formValidate.flag
    };
    data = JSON.stringify(data);
    this.axios
      .post("/risk/busi/getBusiInfo", data, {
        headers: { "Content-Type": "application/json" }
      })
      .then(response => {
        if (response.code == "200") {
          this.cardDatas = response.data;
        }
      });
    this.axios
      .get("/system/getDic/EVAL_STA,BUS_TYPE,IS_STATE,ADJ_STA")
      .then(response => {
        if (response.code == "200") {
          response.data.BUS_TYPE.forEach(element => {
            element.checked = false;
          });
          this.businessTypes = response.data.BUS_TYPE;
          this.evalStatuss = response.data.EVAL_STA;
          this.adjStatuss = response.data.ADJ_STA;
          this.flags = response.data.IS_STATE;
        }
      })
      .catch(function(error) {});
    this.axios
      .post("/system/unit/getExtractUnit")
      .then(response => {
        if (response.code == "200") {
          that.selectTreeCleanUp(response.data);
          that.data1 = response.data;
        }
      })
      .catch(error => {});
    this.axios
      .post("/risk/busi/getRiskLevel")
      .then(response => {
        if (response.code == "200") {
          this.riskLevels = response.data;
        }
      })
      .catch(function(error) {});
  },
  watch: {
    formValidate: {
      handler(newValue, oldValue) {
        let data = {
          busType: this.formValidate.busType, //业务类型
          department: this.formValidate.department, //业务部门
          riskLevel: this.formValidate.riskLevel, //风险等级
          evalStatus: this.formValidate.evalStatus, //评估审核
          adjStatus: this.formValidate.adjStatus, //调整审核
          flag: this.formValidate.flag
        };
        data = JSON.stringify(data);
        this.axios
          .post("/risk/busi/getBusiInfo", data, {
            headers: { "Content-Type": "application/json" }
          })
          .then(response => {
            if (response.code == "200") {
              this.cardDatas = response.data;
            }
          });
      },
      deep: true
    },
    showModal: function(newValue, oldValue) {
      if (!newValue) {
        let data = {
          busType: this.formValidate.busType, //业务类型
          department: this.formValidate.department, //业务部门
          riskLevel: this.formValidate.riskLevel, //风险等级
          evalStatus: this.formValidate.evalStatus, //评估审核
          adjStatus: this.formValidate.adjStatus, //调整审核
          flag: this.formValidate.flag
        };
        data = JSON.stringify(data);
        this.axios
          .post("/risk/busi/getBusiInfo", data, {
            headers: { "Content-Type": "application/json" }
          })
          .then(response => {
            if (response.code == "200") {
              this.cardDatas = response.data;
            }
          });
      }
    },
    "$store.state.firstLoginFlag": {
      handler(newVal) {
        this.firstLoginFlag = newVal;
      }
    }
  },
  data() {
    return {
      firstLoginFlag: this.$store.state.firstLoginFlag,
      dataObjj: "",
      pecentWidth: "80%",
      tabview: "",
      formValidate: {
        busType: [], //业务类型
        department: [], //业务部门
        riskLevel: [], //风险等级
        evalStatus: ["0", "3"], //评估审核
        flag: "", //状态
        adjStatus: ["3"] //调整审核
      },
      formValidate2: {
        id: "",
        flag: true,
        remark: ""
      },
      key: "",
      index: "",
      modal2: false,
      data1: [],
      //业务类型
      businessTypes: [],
      //风险等级
      riskLevels: [],
      evalStatuss: [],
      adjStatuss: [],
      flags: [],
      processSts: "error",
      //当前进行步骤
      showModal: false,
      dataObj: {
        type: "1", //1新增，2修改,3评估 4调整， 5历史记录 6详情
        data: ""
      },
      modelTitle: "新增",
      cardDatas: {}
    };
  },
  methods: {
    showProcess(item, nodeId) {
      this.tabview = "processMessage";
      this.modelTitle = "流程信息";
      this.showModal = true;
      this.dataObj = {
        id: item.id,
        name: "业务洗钱风险评估"
      };
    },
    exportFn(){
      let that = this;
      let data = {
        busType: this.formValidate.busType, //业务类型
        department: this.formValidate.department, //业务部门
        riskLevel: this.formValidate.riskLevel, //风险等级
        evalStatus: this.formValidate.evalStatus, //评估审核
        adjStatus: this.formValidate.adjStatus, //调整审核
        flag: this.formValidate.flag
      };
      data = JSON.stringify(data);
      this.axios
        .post("/risk/busi/exportBusInfo", data, {
         headers: { "Content-Type": "application/json" },
          responseType: "blob"
        })
        .then(response => {
          this.download(response,'导出文档.xls');
        })
        .catch(error => {});
    },
    download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    ok() {
      let data = JSON.stringify(this.formValidate2);
      this.axios
        .post("/risk/busi/updateBusFlag", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") this.$Message.success("更新成功");
        })
        .catch(error => {
          this.$Message.error("更新失败");
          this.cardDatas[this.key][this.index].flag =
            this.cardDatas[this.key][this.index].flag == "0" ? "1" : "0";
        });
    },
    cancel() {
      this.cardDatas[this.key][this.index].flag =
        this.cardDatas[this.key][this.index].flag == "0" ? "1" : "0";
    },
    showAssessRisk(id) {
      this.dataObj = {
        type: "3",
        id: id
      };
      this.tabview = "assessRisk";
      this.modelTitle = "业务洗钱风险评估";
      this.showModal = true;
      this.pecentWidth = "80%";
    },
    selectTreeCleanUp(treeDatas) {
      for (let i = 0; i < treeDatas.length; i++) {
        if (treeDatas[i].children && treeDatas[i].children.length > 0) {
          this.selectTreeCleanUp(treeDatas[i].children);
        } else this.$delete(treeDatas[i], "children");
      }
    },
    getDisplayStatus(item) {
      let status = "-1";
      status = item.type == "1" ? item.evalStatus : item.adjStatus;
      if (status == "1") return "success";
      else if (status == "2") return "process";
      else if (status == "3") return "error";
      else if (status == "0") return "process";
    },
    /**
     * name：改变业务类型状态的
     * index：当前索引
     * param：所有业务类型
     */
    changeSts(index, param) {
      this.businessTypes[index].checked = !this.businessTypes[index].checked;
      let businessType = [];
      this.businessTypes.forEach(e => {
        if (e.checked) businessType.push(e.code);
      });
      this.formValidate.busType = businessType;
    },
    /**
     * name：改变用户停用状态
     */
    changeUseSts(item, key, index) {
      let status = item.type == "1" ? item.evalStatus : item.adjStatus;
      if (status == "2" && item.flag == "0") {
        this.$Message.warning("审核中业务不能进行停用操作");
        this.$nextTick(() => {
          this.cardDatas[key][index].flag = "1";
        });
        return;
      }
      this.modal2 = true;
      this.formValidate2.id = item.id;
      this.formValidate2.flag = item.flag;
      this.formValidate2.remark = "";
      this.key = key;
      this.index = index;
    },
    operate(param, data) {
      if ((param == "edit" || param == "assess" || param == "adjust") && data.flag == "0") {
        this.$Message.warning("停用业务-业务停用不应该进行评估（修改、调整）");
        return;
      }
      let editStatus = true;
      let assessStatus = true;
      let adjustStatus = true;
      if (param != "add") {
        if (data.evalStatus == "2" || data.adjStatus == "2" ) {
          editStatus = false;
        }
        if (data.evalStatus == "2" || data.adjustStatus == "2" || data.adjustStatus =="3") {
          assessStatus = false;
        }
        if (data.evalStatus == "2" || data.evalStatus == "3" ||data.evalStatus == "0" || data.adjStatus == "2") {
          adjustStatus = false;
        }
      }
      switch (param) {
        case "add":
          this.dataObj = {
            type: "1"
          };
          this.tabview = "addBussiness";
          this.modelTitle = "新增";
          this.showModal = true;
          this.pecentWidth = "60%";
          break;
        case "edit":
          if (editStatus) {
            this.dataObj = {
              type: "2",
              id: data.id
            };
            this.tabview = "addBussiness";
            this.modelTitle = "修改";
            this.showModal = true;
            this.pecentWidth = "60%";
          } else {
            this.$Message.info("评估审核中/调整审核中不能修改");
          }
          break;
        case "assess":
          if (assessStatus) {
            this.dataObj = {
              type: "3",
              id: data.id
            };
            this.tabview = "assessRisk";
            this.modelTitle = "业务洗钱风险评估";
            this.showModal = true;
            this.pecentWidth = "80%";
          } else {
            this.$Message.info(
              "待评估状态/评估审核通过/评估审核退回/调整通过才可进行评估"
            );
          }
          break;
        case "adjust":
          if (adjustStatus) {
            this.dataObj = {
              type: "4",
              id: data.id,
              adjStatus: data.adjStatus,
              evalId: data.evalId,
              busName: data.busName,
              riskLevel: data.riskLevel
            };
            this.modelTitle = "调整风险等级";
            this.tabview = "adjustRiskLevel";
            this.showModal = true;
            this.pecentWidth = "75%";
          } else {
            this.$Message.info("待评估状态/评估审核退回/评估审核中/调整审核中不能调整");
          }
          break;
        case "history":
          this.dataObj = {
            type: "5",
            busid: data.id
          };
          this.modelTitle = "历史记录";
          this.tabview = "history";
          this.showModal = true;
          this.pecentWidth = "80%";
          break;
        case "detail":
          this.dataObjj = {
            // type: "6",
            // id: data.id,
            // evalId: data.evalId
            busId: data.id,
            newBusId: "",
            type: "",
            evalBusinessId: "",
            adjBusinessId: ""
          };
          this.modelTitle = "评估详情";
          this.tabview = "assessDetail";
          this.showModal = true;
          this.pecentWidth = "80%";
          break;
      }
    },
    saveDatas() {
      this.showModal = false;
    },
    cancelDialog(flg) {
      this.showModal = flg;
    }
  }
};
</script>
<style lang="scss" @scoped>
.launder-bussiness {
  background: #eef3f7;
  .header {
    height: auto;
    background: #fff;
    padding-left: 15px;
    .content1 {
      .content1-1 {
        display: inline-block;
        .ivu-select {
          padding: 12px 0;
          width: 300px;
        }
        .checked-content {
          display: inline-block;
          &:last-child {
            padding-top: 15px;
            margin-bottom: 15px;
            width: 100%;
          }
          .title {
            display: inline-block;
            margin: 0 15px 0 20px;
            font-size: 16px;
            font-weight: bold;
          }
          .ivu-radio-group {
            label {
              font-size: 14px;
              margin-right: 15px;
              .ivu-radio-checked .ivu-radio-inner {
                border-color: #fa9324;
                &::after {
                  background: #fa9324;
                }
              }
            }
          }
          .ivu-checkbox-group {
            display: inline-block;
            label {
              font-size: 14px;
              .ivu-checkbox-checked .ivu-checkbox-inner {
                background: #fa9324;
                border-color: #fa9324;
              }
            }
          }
        }
      }
      &.border-bottom {
        border-bottom: 1px dashed #eeeeee;
      }
      .contentCellTitle {
        display: inline-block;
        width: 100px;
        font-size: 16px;
        vertical-align: top;
        .contentCellTitle-1 {
          width: 3px;
          height: 18px;
          background: #fff;
        }
      }
      .header-wrapper {
        width: 80%;
        display: inline-block;
        padding: 5px 0;
        .ivu-tag {
          padding: 0px 5px;
          margin: 11px 4px 4px 0px;
          height: 28px;
          line-height: 28px;
          background: #fa9324;
          span {
            &.ivu-tag-text {
              color: #fff;
            }
          }
          &:not(.ivu-tag-border):not(.ivu-tag-dot):not(.ivu-tag-checked) {
            background: 0 0;
            span {
              &.ivu-tag-text {
                color: #b6b6b6;
              }
            }
          }
        }
        span {
          font-size: 14px;
          margin: 0 10px;
          cursor: pointer;
          &.active {
            background: #fa9324;
            border-radius: 2px;
            color: #fff;
          }
        }
      }
    }
  }
  .content {
    .contentCellTitle {
      font-size: 18px;
      .contentCellTitle-1 {
        width: 5px;
        height: 18px;
      }
      &.border-bottom {
        border-bottom: 1px dashed #eef3f8;
      }
    }
    .content-detail {
      margin: 0 -35px 30px 15px;
      overflow: hidden;
      // display: flex;
      // flex-direction: row;
      // flex-wrap: wrap;
      // justify-content: space-between;
      .detail {
        margin: 0 35px 30px 0px;
        float: left;
        width: 400px;
        height: 320px;
        background: #fff;
        position: relative;
        border-radius: 10px;
        .head {
          background: #fff;
          height: 65px;
          border-radius: 10px;
          img {
            height: 60px;
            width: 300px;
          }
          .card-sts {
            display: inline-block;
            position: absolute;
            right: 0;
            border-radius: 0px 5px 0 0;
            &::before {
              position: absolute;
              content: "";
              width: 0;
              height: 0;
              right: 0;
              border-top: 30px solid red;
              border-right: 30px solid red;
              border-bottom: 30px solid transparent;
              border-left: 30px solid transparent;
              border-radius: 0px 5px 0 0;
            }
            span {
              left: -45px;
              transform: rotate(47deg);
              width: 45px;
              font-size: 12px;
              top: 12px;
              color: #fff;
              text-align: center;
            }
          }
          .card-sts1 {
            display: inline-block;
            position: absolute;
            right: 0;
            border-radius: 0px 5px 0 0;
            &::before {
              position: absolute;
              content: "";
              width: 0;
              height: 0;
              right: 0;
              border-top: 30px solid #04bc15;
              border-right: 30px solid #04bc15;
              border-bottom: 30px solid transparent;
              border-left: 30px solid transparent;
              border-radius: 0px 5px 0 0;
            }
            p {
              left: -45px;
              transform: rotate(47deg);
              width: 45px;
              font-size: 12px;
              top: 12px;
              color: #fff;
              text-align: center;
            }
          }
          p {
            position: absolute;
            &.special-title {
              left: 25px;
              top: 20px;
              font-size: 18px;
              color: #fff;
              cursor: pointer;
              display: inline-block;
              height: 18px;
              line-height: 18px;
              overflow: hidden;
              width: 200px;
              text-overflow: ellipsis;
              white-space: nowrap;
            }
            &.ivu-switch {
              top: 22px;
              right: 40px;
            }
            &.ivu-switch-checked {
              border-color: #04bc15;
              background-color: #04bc15;
            }
          }
        }
        .center {
          padding: 10px 0 10px 10px;
          height: 195px;
          display: flex;
          flex-direction: row;
          .left {
            span {
              &.bus-debt {
                color: #727272;
                font-size: 14px;
              }
              &.manage-debt {
                color: #222222;
                font-size: 16px;
                height: 16px;
                line-height: 16px;
                display: inline-block;
                margin-left: 20px;
                width: 250px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
              }
            }
            .steps {
              width: 350px;
              margin: 20px 0 0 0px;
              position: relative;
              left: -10px;
              height: 60px;
              .el-step__head.is-success {
                color: #019fe7;
                border-color: #019fe7;
                cursor: pointer;
              }
              .el-step__head.is-finish {
                cursor: pointer;
              }
              .el-step__title.is-success {
                color: #999;
              }
              // .el-step__line {
              //   // background: #019fe7;
              // }
              .el-step__head.is-process {
                color: #fff;
                .el-step__icon.is-text {
                  background: #019fe7;
                  border-color: #019fe7;
                }
                // .el-step__line {
                // }
              }
              .el-step__title.is-process {
                color: #222222;
              }
              .el-step__main {
                position: relative;
                .el-step__title {
                  font-size: 12px;
                  line-height: 30px;
                  margin-top: 5px;
                }
              }
            }
            .date-content {
              margin-top: 40px;
              span {
                color: #bcbcbc;
                font-size: 14px;
              }
              .assess-date {
                margin-left: 20px;
              }
            }
          }
          .right {
            margin-top: -10px;
            position: relative;
            right: 10px;
            .risk-rank {
              color: #727272;
              font-size: 12px;
              margin-left: -2px;
              width: 50px;
            }
            img {
              height: 140px;
              width: 40px;
              margin-top: 5px;
              margin-left: -5px;
            }
          }
        }
        .bottom {
          height: 60px;
          background: #f7f9fc;
          border-radius: 10px;
          margin-top: 10px;
          .bottom-detail {
            padding-top: 12px;
            margin: 0 40px;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            div {
              text-align: center;
              cursor: pointer;
              .text {
                margin-top: -7px;
              }
            }
          }
        }
      }
    }
  }
}
.border-top {
  border-top: 1px dashed #eeeeee;
}
</style>


