<template>
  <div class="add-bussiness">
    <Form
      ref="formValidate"
      :model="formValidate"
      :rules="ruleValidate"
      :label-width="150"
      class="form-content"
    >
      <FormItem label="业务类型：" prop="busType" >
        <Select v-model="formValidate.busType" placeholder="请输入业务类型">
          <Option v-for="item in busTypes" :value="item.code" :key="item.code">{{item.name}}</Option>
        </Select>
      </FormItem>
      <FormItem label="业务代码：" prop="busCode">
        <Input v-model="formValidate.busCode" placeholder="请输入业务代码" :disabled="disabledFlag"></Input>
      </FormItem>
      <FormItem label="业务名称：" prop="busName">
        <Input v-model="formValidate.busName" placeholder="请输入业务名称"></Input>
      </FormItem>
      <FormItem label="是否允许境外客户：" prop="abroad" style="width:315px">
        <RadioGroup v-model="formValidate.abroad">
          <Radio label="1">是</Radio>
          <Radio label="0">否</Radio>
        </RadioGroup>
      </FormItem>
      <FormItem label="是否业务投资境外：" prop="investmentMark" style="width:315px">
        <RadioGroup v-model="formValidate.investmentMark">
          <Radio label="1">是</Radio>
          <Radio label="0">否</Radio>
        </RadioGroup>
      </FormItem>
      <Row>
        <Col span="11">
          <FormItem label="所属业务部门：" prop="department">
            <treeselect v-model="formValidate.department" :options="data1" placeholder="请选择所属业务部门" />
          </FormItem>
        </Col>
        <Col span="2">
          <span class="speical-span">
            <FormItem></FormItem>
          </span>
        </Col>
        <Col span="11">
          <FormItem label="业务创建日期：">
            <DatePicker
              type="date"
              placeholder="请输入业务创建日期"
              :value="formValidate.createDate"
              @on-change="formValidate.createDate=$event"
            ></DatePicker>
          </FormItem>
        </Col>
      </Row>
      <FormItem label="业务描述：">
        <Input
          v-model="formValidate.busDescribe"
          type="textarea"
          :autosize="{minRows: 2,maxRows: 5}"
          placeholder="请输入业务描述"
        ></Input>
      </FormItem>
      <FormItem>
        <div class="button-content">
          <Button type="primary" @click="handleSubmit('formValidate')">保存</Button>
          <Button @click="handleCancel">取消</Button>
        </div>
      </FormItem>
    </Form>
    <Modal :mask-closable="false"   v-model="switchModal" scrollable title="提示：">
      <div>信息保存成功！</div>
      <div slot="footer">
        <Button @click="cancelModal">取消</Button>
        <Button @click="switchAssessRisk">进行洗钱风险评估</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import Treeselect from "@riophae/vue-treeselect";
import "@riophae/vue-treeselect/dist/vue-treeselect.css";
export default {
  components: { Treeselect },
  props: { dataObj: Object },
  created() {
    let that = this;
    if (this.dataObj.id) {
      this.disabledFlag = true;
      let params = new FormData();
      params.append("id", this.dataObj.id);
      this.axios
        .post("/risk/busi/updateBus", params, {
          headers: { "Content-Type": "multipart/form-data" }
        })
        .then(response => {
          if (response.code == "200") {
            this.formValidate = response.data;
          }
        })
        .catch(error => {});
    }
    this.axios
      .get("/system/getDic/BUS_TYPE")
      .then(response => {
        if (response.code == "200") {
          this.busTypes = response.data.BUS_TYPE;
        }
      })
      .catch(function(error) {});
    this.axios
      .post("/system/unit/getExtractUnit")
      .then(response => {
        if (response.code == "200") {
          this.data1 = response.data;
          this.selectTreeCleanUp(this.data1);
        }
      })
      .catch(error => {});
  },
  data() {
    const validatorBusCode = (rule, value, callback) => {
      if (!this.disabledFlag) {
        let params = new FormData();
        params.append("busCode", value);
        this.axios
          .post("/risk/busi/getBusCode", params, {
            headers: { "Content-Type": "multipart/form-data" }
          })
          .then(response => {
            if (response.data == "201") {
              callback(new Error("业务代码已存在"));
            } else {
              callback();
            }
          })
          .catch(error => {});
      } else {
        callback();
      }
    };
    const validaterDepartment = (rule, value, callback) => {
      if (this.formValidate.department == undefined) {
        callback(new Error("请选择提取机构"));
      } else {
        callback();
      }
    };
    return {
      switchModal: false,
      disabledFlag: false,
      formValidate: {
        busCode: "",
        abroad: "",
        investmentMark: "",
        busType: "",
        busName: "",
        department: null,
        createDate: "",
        busDescribe: ""
      },
      busTypes: [],
      data1: [],
      ruleValidate: {
        busType: [
          {
            required: true,
            message: "业务类型不能为空！",
            trigger: "change"
          }
        ],
        abroad: [
          {
            required: true,
            message: "境内外客户不能为空！",
            trigger: "change"
          }
        ],
        investmentMark: [
          {
            required: true,
            message: "业务投资境外不能为空！",
            trigger: "change"
          }
        ],
        busCode: [
          {
            required: true,
            message: "业务代码不能为空！",
            trigger: "blur"
          },
          {
            type: "string",
            max: 6,
            message: "长度最大不能超过6个字！",
            trigger: "blur"
          },
          {
            validator: validatorBusCode,trigger: "xxxx"
            
          }
        ],
        busName: [
          {
            required: true,
            message: "业务名称不能为空！",
            trigger: "blur"
          },
          {
            type: "string",
            max: 64,
            message: "长度最大不能超过64个字！",
            trigger: "blur"
          }
        ],
        department: [
          {
            required: true,
            message: "请选择所属业务部门！",
            validator: validaterDepartment,
            trigger: "xxxx"
          }
        ],
        busDescribe: [
          {
            type: "string",
            max: 512,
            message: "业务描述不能超过512个字！",
            trigger: "blur"
          }
        ]
      }
    };
  },
  mounted() {},
  watch: {
    "formValidate.department": function(newValue, oldValue){
      this.$refs["formValidate"].validateField("department");
    },
    switchModal(a) {
      if (a == false) {
        this.$emit("cancel", false);
      }
    }
  },
  methods: {
    cancelModal() {
      this.switchModal = false;
    },
    selectTreeCleanUp(treeDatas) {
      for (let i = 0; i < treeDatas.length; i++) {
        if (treeDatas[i].children && treeDatas[i].children.length > 0) {
          this.selectTreeCleanUp(treeDatas[i].children);
        } else this.$delete(treeDatas[i], "children");
      }
    },
    switchAssessRisk() {
      this.switchModal = false;
      this.$emit("switchAssessRisk", this.dataObj.id);
    },
    handleSubmit(name) {
      let data = JSON.stringify(this.formValidate);
      this.$refs[name].validate(valid => {
        if (valid) {
          this.axios
            .post("/risk/busi/saveBusInfo", data, {
              headers: { "Content-Type": "application/json" }
            })
            .then(response => {
              if (response.code == "200") {
                this.dataObj.id = response.data.id;
                this.switchModal = true;
                this.$Message.success("保存成功");
              }
            })
            .catch(error => {});
        }
      });
    },
    handleCancel(name) {
      this.$emit("cancel", false);
    }
  }
};
</script>
<style lang="scss" scoped>
.add-bussiness {
  label {
    font-size: 14px;
  }
  .form-content {
    .speical-span {
      width: 8.33333333%;
      float: left;
    }
    .ivu-date-picker {
      width: 100%;
    }
  }
}
</style>


