<template>
  <div class="history-detail">
    <div class="content flexStart">
      <div class="left">
        <Form :label-width="100">
          <FormItem label="操作时间">
            <label>{{arr1.updateTime}}</label>
          </FormItem>
          <FormItem label="业务类型">
            <label>{{arr1.busTypeName}}</label>
          </FormItem>
          <FormItem label="业务代码">
            <label>{{arr1.busCode}}</label>
          </FormItem>
          <FormItem label="业务名称">
            <label>{{arr1.busName}}</label>
          </FormItem>
          <FormItem label="所属业务部门">
            <label>{{arr1.dpname}}</label>
          </FormItem>
          <FormItem label="业务创建日期">
            <label>{{arr1.createDate}}</label>
          </FormItem>
          <FormItem label="业务描述">
            <label>{{arr1.busDescribe}}</label>
          </FormItem>
        </Form>
      </div>
      <div class="line"></div>
      <div class="right">
        <Form :label-width="100">
          <FormItem label="操作时间">
            <label>{{arr2.updateTime}}</label>
          </FormItem>
          <FormItem label="业务类型">
            <label>{{arr2.busTypeName}}</label>
          </FormItem>
          <FormItem label="业务代码">
            <label>{{arr2.busCode}}</label>
          </FormItem>
          <FormItem label="业务名称">
            <label>{{arr2.busName}}</label>
          </FormItem>
          <FormItem label="所属业务部门">
            <label>{{arr2.dpname}}</label>
          </FormItem>
          <FormItem label="业务创建日期">
            <label>{{arr2.createDate}}</label>
          </FormItem>
          <FormItem label="业务描述">
            <label>{{arr2.busDescribe}}</label>
          </FormItem>
        </Form>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ["dataObjj"],
  created() {
    var that = this;
    let data = {
      id: this.dataObjj.id,
      newId: this.dataObjj.newId
    };
    data = JSON.stringify(data);
    this.axios
      .post("/risk/busi/busRiskInfoHisComPare", data, {
        headers: { "Content-Type": "application/json" }
      })
      .then(response => {
        if (response.code == "200") {
            
          if (response.data.length > 1) {
             
            that.arr1 = response.data[0];
            that.arr2 = response.data[1];
            that.arr1.createDate = that.arr1.createDate.substr(0,10);
            that.arr2.createDate = that.arr2.createDate.substr(0,10);
          } else if (response.data.length > 0) {
           
            that.arr1 = response.data[0];
            that.arr1.createDate = that.arr1.createDate.substr(0,10);
          }
        }
      })
      .catch(function(error) {});
  },
  data() {
    return {
      arr1: 
        {
          updateTime: "",
          busTypeName: "",
          busCode: "",
          busName: "",
          dpname: "",
          createDate: "",
          busDescribe: ""
        }
      ,
      arr2: 
        {
          updateTime: "",
          busTypeName: "",
          busCode: "",
          busName: "",
          dpname: "",
          createDate: "",
          busDescribe: ""
        }
      
    };
  }
};
</script>
<style lang="scss">
.history-detail {
  .left{
    width:50%;
  }
  .right{
    width:50%;
    padding-left:8%;
    box-sizing: border-box;
  }
  .content {
    display: flex;
    justify-content: space-around;
    .line {
      width: 1px;
      background: #e8eaec;
      margin-top: -16px;
    }
    .ivu-form-item-label {
      font-size: 14px !important;
      text-align: left;
    }
    .ivu-form-item-content {
      label {
        font-size: 14px !important;
      }
    }
  }
}
</style>


