<template>
  <div class="history">
    <div class="contentCellTitle">
      <div class="contentCellTitle-1"></div>修改历史
    </div>
    <table class="tableBox" cellspacing="0" cellpadding="0">
      <thead>
        <tr>
          <td v-for="(item,index) in tableHeader1">{{item.name}}</td>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item,index) in tableBody1">
          <td>{{index+1}}</td>
          <td>{{item.dpname}}</td>
          <td>{{item.updateTime}}</td>
          <td>{{item.updater}}</td>
          <td class="spanBox" @click="shhowHistory(item.id,item.newId)">详情</td>
        </tr>
      </tbody>
    </table>
    <div class="contentCellTitle">
      <div class="contentCellTitle-1"></div>评估历史
    </div>
    <div class="contentCellBody">
      <table class="tableBox" cellspacing="0" cellpadding="0">
        <thead>
          <tr>
            <td v-for="(item,index) in tableHeader2">{{item.name}}</td>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item,index) in tableBody2">
            <td>{{index+1}}</td>
            <td >
              <el-popover
              placement="top-start"
              width="200"
              trigger="hover"
              :content="item.department">
              <span slot="reference" class="textMaxWidth disBlock textOverflow">{{item.department}}</span>
              </el-popover>
            </td>
            <td>{{item.type}}</td>
            <td>{{item.totalScore}}</td>
            <td>{{item.sysLevel}}</td>
            <td class="noWrap">{{item.evalDate?item.evalDate.substr(0,10):''}}</td>
            <td>{{item.evalUpdater}}</td>
            <td >
              <el-popover
              placement="top-start"
              width="200"
              trigger="hover"
              :content="item.evalDpName">
              <span slot="reference" class="textMaxWidth disBlock textOverflow">{{item.evalDpName}}</span>
              </el-popover>
            </td>
            <td>{{item.riskLevel}}</td>
            <td>{{item.evalRemark}}</td>
            <td class="spanBox" @click="watchFiles(item.evalBusinessId)" v-if="item.evalBusinessId">查看</td>
            <td  v-else>暂无</td>
            <td>{{item.adjustLevel}}</td>
            <td class="noWrap">{{item.adjTime?item.adjTime.substr(0,10):''}}</td>
            <td>{{item.adjCreator}}</td>
            <td >
              <el-popover
              placement="top-start"
              width="200"
              trigger="hover"
              :content="item.adjDpName">
              <span slot="reference" class="textMaxWidth disBlock textOverflow">{{item.adjDpName}}</span>
              </el-popover>
            </td>
            <td>{{item.causeType}}</td>
            <td>{{item.causeDetail}}</td>
            <td class="spanBox" @click="watchFiles(item.adjBusinessId)" v-if="item.adjBusinessId">查看</td>
            <td  v-else>暂无</td>
            <td class="spanBox" @click="search(item.type,item.evalBusinessId,item.adjBusinessId)">查看详情</td>
            <!-- 查看详情type如果是1，传evalBusinessId，如果是2就传adjBusinessId -->
          </tr>
        </tbody>
      </table>
    </div>
    <div class="contentCellTitle">
      <div class="contentCellTitle-1"></div>停/启用历史记录
    </div>
    <table class="tableBox" cellspacing="0" cellpadding="0">
      <thead>
        <tr>
          <td v-for="(item,index) in tableHeader3">{{item.name}}</td>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item,index) in tableBody3">
          <td>{{index+1}}</td>
          <td>{{item.flag}}</td>
          <td class="textOverflow textmaxwidth" :title="item.remark">{{item.remark}}</td>
          <td>{{item.updater}}</td>
          <td>{{item.updateTime}}</td>
        </tr>
      </tbody>
    </table>
    <Modal :mask-closable="false"  
      v-model="showModal"
      :title="modelTitle"
      @on-cancel="cancel"
      class="dialog"
      :styles="{width: percent}"
    >
      <component :is="tabview" v-if="showModal" :data-objj="dataObjj"></component>
      <div slot="footer"></div>
    </Modal>
    <Modal :mask-closable="false"   v-model="modal1" title="文件列表">
        <div class="fujian">
        <div class="fujian-title">附件:</div>
        <div v-for="(item,index) in fileList" :key="index">
          <span
            style="width:100%;height:40px;line-height:20px;overflow:hidden;text-overflow: ellipsis;margin-bottom:5px"
            class="cursorPointer"
            @click="fileDownload(item.fileId, item.filename)"
          >{{item.filename}}</span>
        </div>
      </div>
      <div slot="footer">
      </div>
    </Modal>
  </div>
</template>
<script>
import historyDetail from "./historyDetaill/historyDetail";
import processMessage from "./historyDetaill/historyMessage"; //过程信息

export default {
  props: ["dataObj"],
  created() {
    var that = this;
    this.axios
      .post(`/risk/busi/busFlagHis?id=${this.dataObj.busid}`)
      .then(response => {
        if (response.code == "200") {
          that.tableBody3 = response.data;
        }
      })
      .catch(error => {});
    this.axios
      .post(`/risk/busi/busRiskInfoHis?id=${this.dataObj.busid}`)
      .then(response => {
        if (response.code == "200") {
          that.tableBody1 = response.data;
        }
      })
      .catch(error => {});
      this.axios
      .post(`/risk/busi/busRiskEvalAndAdjHis`, JSON.stringify({busid: this.dataObj.busid}), {
            headers: { "Content-Type": "application/json" }
      })
      .then(response => {
        if (response.code == "200") {
          that.tableBody2 = response.data;
        }
      })
      .catch(error => {});
      
  },
  data() {
    return {
      percent: "80%",
      showModal: false,
      modal1: false,
      modelTitle: "详情",
      tabview: "historyDetail",
      tableHeader1: [
        { name: "序" },
        { name: "部门" },
        { name: "更新时间" },
        { name: "更新人员" },
        { name: "更新内容" },
      ],
      fileList: [],
      dataObjj: {},
      tableBody1: [
        // { dpname: "", updateTime: "", updater: "" }
      ],
      tableHeader2: [
        { name: "序" },
        { name: "部门" },
        { name: "评估类型" },
        { name: "综合评分" },
        { name: "系统评估等级" },
        { name: "评估日期" },
        { name: "评估人员" },
        { name: "评估人员所属部门" },
        { name: "洗钱风险等级" },
        { name: "评估备注" },
        { name: "评估附件" },
        { name: "调整等级" },
        { name: "调整日期" },
        { name: "调整人员" },
        { name: "调整人员所属部门"},
        { name: "调整事由" },
        { name: "调整事由说明" },
        { name: "调整附件" },
        { name: "流程详情" }
      ],
      tableBody2: [
        // {
        //   department: "",
        //   type: "",
        //   totalScore: "",
        //   sysLevel: "",
        //   evalDate: "",
        //   evalUpdater: "",
        //   riskLevel: "",
        //   evalRemark: "",
        //   evalFileName: [],
        //   adjustLevel: "",
        //   adjTime: "",
        //   adjCreator: "",
        //   causeType: "",
        //   causeDetail: "",
        //   adjFileName: []
        // }
      ],
      tableHeader3: [
        { name: "序" },
        { name: "状态" },
        { name: "备注" },
        { name: "修改人" },
        { name: "停/启用时间" }
      ],

      tableBody3: [
        // { flag: "", remark: "", updater: "", updateTime: "" }
      ]
    };
  },
  components: {
    historyDetail,
    processMessage
  },
  methods: {
    shhowHistory(a,b) {
      this.dataObjj={
        id:a,
        newId:b,
      }
      this.showModal = true;
      this.tabview = "historyDetail";
      this.modelTitle = "详情";
      this.percent = "60%";
    },
    search(a,b,c) {
       this.dataObjj={
        type:a,
        evalBusinessId:b,
        adjBusinessId:c,
      }
      this.tabview = "processMessage";
      this.modelTitle = "流程信息";
      this.showModal = true;
      this.percent = "80%";
      // item.type,item.evalBusinessId,item.adjBusinessId
    },
    watchFiles(id) {
      this.fileList = [];
      if(id){
        this.axios
        .post(`/system/remote/getFileList/${id}`)
        .then(res => {
          if (res.code == "200") {
            this.fileList = res.data;
          }
        })
        .catch(function(error) {});
      }
      this.modal1 = true;
    },
    fileDownload(filePath, fileName) {
      let that = this;
      let data = JSON.stringify({
        "token": "",
        "fileId": filePath
      });
      this.axios
        .post("/system/remote/micDownLoad", data, {
          headers: { "Content-Type": "application/json" },
        })
        .then(response => {
            let data2 = JSON.stringify({
              "url": response.message,
              "fileName": fileName
            });
             this.axios
              .post(`/system/remote/getFileInfo`,data2,
              {
                  headers: { "Content-Type": "application/json" },
                  responseType: "blob"
              }
              )
              .then(res =>{
                  this.download(res, fileName);
              })
              .catch(function(error) {});
        })
        .catch(error => {});
    },
    download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    cancel() {}
  }
};
</script>
<style lang="scss">
.history {
  .tab-title {
    font-size: 14px;
    color: #333;
    font-weight: bold;
  }
  table {
    width: 100%;
    font-size: 14px;
    color: #333333;
    margin-bottom: 20px;
    margin-top: 0;
    .show-detail {
      color: #0000cc;
      cursor: pointer;
      text-align: center;
    }
    &.first-tab {
      td {
        border: 0.5px solid #ebecee;
        height: 30px;
        line-height: 30px;
        padding-left: 20px;
        &:nth-child(odd) {
          font-weight: bold;
        }
      }
    }
    &.second-tab {
      thead {
        tr {
          td {
            font-weight: bold;
          }
        }
      }
      tbody {
        td {
          &:nth-child(odd) {
            font-weight: normal;
          }
        }
      }
    }
  }
}
</style>


